import React, { ReactNode, useEffect, useRef, useState } from 'react';

interface Props {
  children: ReactNode;
  aspectRatio: number;
}

/**
 * This image extends GatsbyImage from gatsby-image-plugin, as untill now I've not found a working solution for GatsbyImage to work with resizing the window and maintaining aspect ratio, without javascript.
 * So short version, this component helps with aspect ratio.
 */
const AspectRatioWrapper: React.FC<Props> = ({ aspectRatio, children }) => {
  const ref = useRef<HTMLDivElement>(null);
  const [width, setWidth] = useState(0);
  useEffect(() => {
    ref.current?.offsetWidth && setWidth(ref.current.offsetWidth);
  }, []);

  return (
    <div ref={ref} style={{ height: width * aspectRatio }}>
      {children}
    </div>
  );
};

export default AspectRatioWrapper;
