import styled from '@emotion/styled';
import React, { ComponentProps, ReactNode } from 'react';
import { WidthBoundary } from '../View';

const Container = styled.div`
  padding-left: 32px;
  padding-right: 32px;
`;

const DefaultContainer: React.FC<{
  widthBoundaryProps?: ComponentProps<typeof WidthBoundary>;
  children: ReactNode;
  enable?: boolean;
}> = ({
  children,
  enable = true,
  widthBoundaryProps = { maxWidth: `1392px` },
}) =>
  enable ? (
    <WidthBoundary {...widthBoundaryProps}>
      <Container>{children}</Container>
    </WidthBoundary>
  ) : (
    <>{children}</>
  );

export default DefaultContainer;
