import styled from '@emotion/styled';
import { Button, Drawer } from '@mui/material';
import VTLogo from '../../assets/svg/byvtLogo.svg';
import React, { ComponentProps, useState } from 'react';
import IconButton from '../IconButton';
import PageLinks from './PageLinks';
import { colors, typography } from '@/theme';
import { usePageContext } from '@/utils/PageContext';

const VTLogoResized = styled(VTLogo)`
  transform: scale(1.2);
  margin-top: 16px;
  margin-bottom: 16px;
`;

const SocialIcons: React.FC<{
  socialLinks: {
    href: string;
    iconName: ComponentProps<typeof IconButton>['iconName'];
  }[];
}> = ({ socialLinks }) => {
  const Container = styled.div`
    flex-shrink: 0;
    margin-top: 32px;
  `;

  const SocialLink = styled.a`
    margin: 4px;
  `;

  return (
    <Container>
      {socialLinks.map(({ href, iconName }) => (
        <SocialLink key={href} href={href}>
          <IconButton iconName={iconName} />
        </SocialLink>
      ))}
    </Container>
  );
};

const Container = styled.div({
  display: `flex`,
  flexDirection: `column`,
  alignItems: `center`,
  padding: `8px`,
});

const Nav = styled.nav({
  display: `flex`,
  flexDirection: `column`,
  alignItems: `center`,
  [`> a`]: {
    fontSize: `26px`,
    padding: `0.8em 0 0.2em 0`,
  },
});

const LogoContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const CallToAction = styled(Button)({
  backgroundColor: colors.primary,
  flexShrink: 0,
  ...typography.button,
  color: colors.textInverted,
  marginTop: `32px`,
  fontSize: `18px`,
  padding: `12px 8px`,
});

interface Props {
  links: { title: string; slug: string }[];
  socialLinks: ComponentProps<typeof SocialIcons>['socialLinks'];
  currentSlug: string;
  actionLink: { title: string };
  showCTA?: boolean;
}

const NavigationMenu: React.FC<Props> = ({
  links,
  socialLinks,
  currentSlug,
  actionLink,
  showCTA,
}) => {
  const [open, setOpen] = useState(false);

  const actions = usePageContext();

  return (
    <div>
      <IconButton
        style={{ position: `absolute`, top: 56, left: 28 }}
        iconName="FaGripLines"
        onPress={() => {
          setOpen(true);
        }}
      />
      <LogoContainer>
        <VTLogoResized />
      </LogoContainer>
      <Drawer
        sx={{
          position: `relative`,
          '.MuiBackdrop': {
            backgroundColor: `rgba(0, 0, 0, 0.3)`,
          },
          '.MuiPaper-root': {
            top: 0,
          },
        }}
        open={open}
        anchor={`bottom`}
        onClose={() => setOpen(false)}
      >
        <div>
          <IconButton
            style={{ position: `absolute`, left: 28, top: 56 }}
            iconName="FaTimes"
            onPress={() => {
              setOpen(false);
            }}
          />
          <Container>
            <VTLogoResized />
            <Nav>
              <PageLinks links={links} currentSlug={currentSlug} />
            </Nav>
            <SocialIcons socialLinks={socialLinks} />
            {showCTA && (
              <CallToAction onClick={actions.openContactPopUp}>
                {actionLink.title}
              </CallToAction>
            )}
          </Container>
        </div>
      </Drawer>
    </div>
  );
};

export default NavigationMenu;
