import React, { ReactNode } from 'react';
import { IconContext } from 'react-icons';
import {
  FaFacebookF,
  FaInstagram,
  FaEnvelope,
  FaWhatsapp,
  FaGripLines,
  FaTimes,
} from 'react-icons/fa';
import MUIIconButton from '@mui/material/IconButton';
import { colors } from '@/theme';

type IconNames =
  | 'FaFacebookF'
  | 'FaInstagram'
  | 'FaEnvelope'
  | 'FaWhatsapp'
  | 'FaGripLines'
  | 'FaTimes';

interface Props {
  iconName: IconNames;
  onPress?: () => void;
  style?: React.CSSProperties;
}
const icons: { [key in IconNames]: ReactNode } = {
  FaEnvelope: <FaEnvelope />,
  FaFacebookF: <FaFacebookF />,
  FaInstagram: <FaInstagram />,
  FaWhatsapp: <FaWhatsapp />,
  FaGripLines: <FaGripLines />,
  FaTimes: <FaTimes />,
};

const IconButton: React.FC<Props> = ({ iconName, onPress, style }) => {
  const icon = icons[iconName];
  return (
    <MUIIconButton style={style} onClick={onPress}>
      <IconContext.Provider value={{ color: colors.iconDefault }}>
        {icon}
      </IconContext.Provider>
    </MUIIconButton>
  );
};

export default IconButton;
