import React, { ComponentProps } from 'react';
import styled from '@emotion/styled';
import IconButton from '../IconButton';
import VTLogo from '../../assets/svg/byvtLogo.svg';
import { colors } from '@/theme';
import { Button } from '@mui/material';
import PageLinks from './PageLinks';
import { usePageContext } from '@/utils/PageContext';

const Container = styled.div`
  display: flex;
  width: calc(100% - 32px);
  justify-content: space-between;
  align-items: center;
  padding-left: 16px;
  padding-right: 16px;
`;

const SpaceFiller = styled.div`
  width: 175px;
`;

const SocialIcons: React.FC<{
  socialLinks: {
    href: string;
    iconName: ComponentProps<typeof IconButton>['iconName'];
  }[];
}> = ({ socialLinks }) => {
  const Container = styled.div`
    flex-shrink: 0;
    width: 175px;
  `;

  return (
    <Container>
      {socialLinks.map(({ href, iconName }) => (
        <a key={href} href={href}>
          <IconButton iconName={iconName} />
        </a>
      ))}
    </Container>
  );
};

const CenterWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const CallToAction = styled(Button)({
  backgroundColor: colors.primary,
  flexShrink: 0,
  width: `175px`,
});

const Nav = styled.nav({
  display: `flex`,
  flexWrap: `wrap`,
  justifyContent: `center`,
});

interface Props {
  links: { title: string; slug: string }[];
  socialLinks: ComponentProps<typeof SocialIcons>['socialLinks'];
  currentSlug: string;
  showCTA?: boolean;
}

const NavigationBar: React.FC<Props> = ({
  links,
  socialLinks,
  currentSlug,
  showCTA,
}) => {
  const actions = usePageContext();

  return (
    <Container>
      <SocialIcons socialLinks={socialLinks} />
      <CenterWrapper>
        <VTLogo />
        <Nav>
          <PageLinks links={links} currentSlug={currentSlug} />
        </Nav>
      </CenterWrapper>

      {showCTA ? (
        <CallToAction
          size="medium"
          variant="contained"
          onClick={actions.openContactPopUp}
        >
          Neem contact op
        </CallToAction>
      ) : (
        <SpaceFiller />
      )}
    </Container>
  );
};

export default NavigationBar;
