import styled from '@emotion/styled';
import React, { ReactNode, useMemo } from 'react';

interface Props {
  children: ReactNode[];
  spaceBetween: number;
}

const List: React.FC<Props> = ({ children, spaceBetween }) => {
  const validChildren = useMemo(() => {
    if (children) {
      return Array.isArray(children)
        ? children.filter((item) => !!item)
        : [children];
    }
  }, [children]);

  const { ChildContainer, Container } = {
    ChildContainer: styled.div`
      :first-of-type {
        margin-top: 0;
      }
      :last-of-type {
        margin-bottom: 0;
      }
      margin-top: ${spaceBetween / 2}px;
      margin-bottom: ${spaceBetween / 2}px;
    `,
    Container: styled.div`
      width: 100%;
    `,
  };

  return validChildren ? (
    <Container>
      {validChildren.map((child, index) => (
        <ChildContainer key={`listChild${index}`}>{child}</ChildContainer>
      ))}
    </Container>
  ) : (
    <></>
  );
};

export default List;
