import React from 'react';
import styled from '@emotion/styled';
import { typography, colors } from '@/theme';
import { Link } from 'gatsby';

const PageLink = styled(Link)(({ selected }: { selected: boolean }) => {
  const selectedUnderline = selected && {
    backgroundImage: `linear-gradient(${colors.primary},${colors.primary})`,
    backgroundRepeat: `repeat-x`,
    backgroundSize: `1px 1px`,
    backgroundPosition: `0 100%`,
  };

  return {
    ...typography.nav,
    [`:active`]: {
      color: colors.primary,
    },
    margin: `0 1.1vw`,
    padding: `0.7em 0`,
    textDecoration: `none`,
    ...selectedUnderline,
  };
});

const PageLinks: React.FC<{
  links: { title: string; slug: string }[];
  currentSlug: string;
}> = ({ links, currentSlug }) => {
  return (
    <>
      {links.map((link) => (
        <PageLink
          to={`/${link.slug}`}
          selected={currentSlug === link.slug}
          key={link.title}
        >
          {link.title}
        </PageLink>
      ))}
    </>
  );
};
export default PageLinks;
