import { useStaticQuery, graphql } from 'gatsby';
import IconButton from '@/components/IconButton';
import NavigationBar from './NavigationBar';
import React, { ComponentProps } from 'react';
import { Max1023View, Min1024View, WidthBoundary } from '../View';
import NavigationMenu from './NavigationMenu';
import { colors } from '@/theme';

const socialLinks: {
  href: string;
  iconName: ComponentProps<typeof IconButton>['iconName'];
}[] = [
  {
    href: `https://www.facebook.com/instagrambyvtphotography`,
    iconName: `FaFacebookF`,
  },
  {
    href: `https://www.instagram.com/byvtphotography/?hl=nl`,
    iconName: `FaInstagram`,
  },
  {
    href: `mailto:info@byvtphotography.nl`,
    iconName: `FaEnvelope`,
  },
  {
    href: `https://wa.me/31657235511`,
    iconName: `FaWhatsapp`,
  },
];

const Navigation: React.FC<{
  showCTA?: boolean;
  location: Location;
  transparent?: boolean;
}> = ({ location, showCTA = true, transparent = false }) => {
  const data: {
    allContentfulPage: {
      nodes: {
        title: string;
        slug: string;
        showInNavigation: boolean;
        index: number;
      }[];
    };
  } = useStaticQuery(graphql`
    query HeaderQuery {
      allContentfulPage {
        nodes {
          title
          showInNavigation
          slug
          index
        }
      }
    }
  `);

  const links: { title: string; slug: string }[] = [
    ...data.allContentfulPage.nodes
      .filter(({ showInNavigation }) => showInNavigation)
      .sort((prev, curr) => (curr.index < prev.index ? 1 : -1))
      .map(({ slug, title }) => ({
        title,
        slug: slug === `home` ? `` : slug,
      })),
    { title: `Contact`, slug: `contact` },
  ];

  const currentSlug = location.pathname.substring(
    location.pathname.lastIndexOf(`/`) + 1,
  );

  return (
    <WidthBoundary
      backgroundStyle={
        !transparent
          ? { backgroundColor: colors.background }
          : {
              zIndex: 10,
              position: `absolute`,
              background: `linear-gradient(to bottom, rgba(255,255,255,0.7) 0%,rgba(255,255,255,0) 100%)`,
            }
      }
    >
      <Min1024View>
        <NavigationBar
          links={links}
          socialLinks={socialLinks}
          currentSlug={currentSlug}
          showCTA={showCTA}
        />
      </Min1024View>
      <Max1023View>
        <NavigationMenu
          links={links}
          socialLinks={socialLinks}
          currentSlug={currentSlug}
          actionLink={{ title: `Neem contact op` }}
          showCTA={showCTA}
        />
      </Max1023View>
    </WidthBoundary>
  );
};

export default Navigation;
