import React from 'react';
import styled from '@emotion/styled';
import { colors } from '@/theme';

export const Min1024View = styled.div`
  @media only screen and (max-width: 1023px) {
    display: none;
  }
`;
export const Max1023View = styled.div`
  @media only screen and (min-width: 1024px) {
    display: none;
  }
`;

export const Min768View = styled.div`
  @media only screen and (max-width: 767px) {
    display: none;
  }
`;
export const Max767View = styled.div`
  @media only screen and (min-width: 768px) {
    display: none;
  }
`;

export type MaxWidth = '1392px' | '768px';

export const WidthBoundary: React.FC<{
  backgroundStyle?: React.CSSProperties;
  backgroundColor?: 'white' | 'tinted';
  children?: React.ReactNode;
  maxWidth?: MaxWidth;
}> = ({ backgroundStyle, backgroundColor, children, maxWidth = `1392px` }) => {
  const Background = styled.div({
    display: `flex`,
    justifyContent: `center`,
    width: `100%`,
    backgroundColor:
      backgroundColor === `tinted` ? colors.backgroundTint : colors.background,
    ...backgroundStyle,
  });
  const WidthBoundary = styled.div`
    max-width: ${maxWidth};
    width: 100%;
  `;
  return (
    <Background>
      <WidthBoundary>{children}</WidthBoundary>
    </Background>
  );
};
